<template>
    <div class="main" id="test">
        <div class="moon" style="" id="center">
            <canvas id="myCanvas" resize="true"></canvas>
            <img id="bg1" style="display: none" class="visuallyhidden" src="@/images/sbg1.png" width="320" height="491">
            <img id="bg2" style="display: none" class="visuallyhidden" src="@/images/bg2.png" width="320" height="491">
            <img id="bg3" style="display: none" class="visuallyhidden" src="@/images/bg3.png" width="320" height="491">
            <img id="bg4" style="display: none" class="visuallyhidden" src="@/images/bg4.png" width="320" height="491">
        </div>
        <p class="bottom-desc"></p>
        <div id="fugai" style="position:fixed;z-index:10000;width:100vw;height:100vh;top:0;left:0;"></div>
    </div>
</template>

<script>
    import paper from 'paper'
    import html2canvas from 'html2canvas'
    export default {
        name: "playpage",
        methods:{
            anmination(){
                paper.install(window)
                paper.setup(document.getElementById('myCanvas'))
                paper.settings.handleSize = 0;
                paper.settings.hitTolerance = 4;
            },
            paperStart(){
               // require('../js/myPaper.js')
                var h = window.outerHeight

                function circleWidth(width = 0.6){
                    if( h <= 580 ){
                        console.log('??')
                    }

                    var view_width = ( view.size.width/2 ) * width
                    return view_width
                }

                function opacityToZero(path,velocity = 0.01){
                    if(path.opacity <= 0.02){ path.opacity = 0;  return }
                    path.opacity -= velocity
                }

                function opacityToOne(path, velocity = 0.01){
                    path.opacity += velocity
                    if(path.opacity >= 0.9){ path.opacity = 1;  return }
                }

                function addCirclePoint(obj,times){
                    var hudu = (2*Math.PI / 360)
                    var x, y
                    for(var i = 0;i<times;i++){
                        var z = 360/times * i
                        x = Math.round( Math.cos(hudu*z)*circleWidth(1) +view.center.x)
                        y = Math.round( Math.sin(hudu*z)*circleWidth(1) +view.center.y)
                        var point = new Point(x, y)
                        obj.add(point)
                    }
                }

                function addCircleLine(segments,obj){
                    var list = []
                    for(var i = 0;i<90;i++){  //50 50 !!!!!!!
                        var path = new Path()
                        path.add(view.center)
                        path.lineTo(segments[i].point)
                        var ints = path.getIntersections(obj)
                        var ints_circle = path.getIntersections(outer_circle)
                        //console.log(ints[0].point)
                        if(0 in ints){
                            var link2 = new Path()
                            link2.strokeWidth = 2
                            link2.opacity = 1 //
                            link2.add(ints_circle[ints_circle.length - 1].point)
                            link2.lineTo(ints[ints.length - 1].point)
                            list.push(link2)
                            listLength += link2.length
                        }else{
                            var link = new Path()
                            link.strokeWidth = 2
                            link.opacity = 1 //
                            link.add(ints_circle[ints_circle.length - 1].point)
                            link.lineTo(view.center)
                            list.push(link)
                            listLength += link.length
                        }
                    }

                    return list
                }

//
                var outer_circle = new Path.Circle({
                    center: view.center,
                    radius: circleWidth(),
                    strokeColor: '#8B572A',
                    strokeWidth: 1
                })

//
                var yd = new Path.Circle({
                    center: view.center,
                    radius: 2,
                    strokeColor: '#8B572A',
                    strokeWidth: 2,
                    fillColor: '#8B572A'
                })

//
                var outer_point = new Path()
                addCirclePoint(outer_point,90)
//outer_point.add(new Point(Math.cos(50)*circleWidth(),Math.sin(50)*circleWidth()))

//
                var pointer = new Path(view.center, new Point(view.center.x-circleWidth(),view.center.y))
                pointer.strokeColor = '#8B572A'
                pointer.strokeWidth = 1.5
                pointer.rotate(180, view.center)

//
                var users_path = new Path({
                    strokeColor: '#F59A55',
                    strokeWidth: 2
                })

                var user_point_list = 0


                var pointer_init_count = 0  //
                function begin(){
                    opacityToZero(outer_circle, 0.01) //
                    if(user_point_list<360){
                        pointer.rotate(4, view.center)
                        user_point_list += 4;
                    }
                    if(user_point_list >=360){
                        document.querySelector("#fugai").style.display = 'none'
                    }
                }


                view.onMouseDown = function (event) {
                    users_path.add(event.point)
                    console.log('dw')
                }

                view.onMouseDrag = function (event) {
                    users_path.add(event.point)
                    user_point_list += 1
                    console.log('drag')
                }


                var circle_line_list = []
                view.onMouseUp = function (){
                    users_path.closePath()
                    console.log(outer_point.segments)
                    circle_line_list = addCircleLine(outer_point.segments,users_path)
                    begin_result = true
                }

                var time = 0  //Init
                var hudu = 0 //
                var begin_result = false
                var begin_init = true
                var circle_line_list_length = 0
                var addmooncake_font = false
                var listLength = 0

                function randomNum(minNum,maxNum){
                    switch(arguments.length){
                        case 1:
                            return parseInt(Math.random()*minNum+1,10);
                            break;
                        case 2:
                            return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10);
                            break;
                        default:
                            return 0;
                            break;
                    }
                }
                view.onFrame = function onFrame(event){

                    if(!begin_result && begin_init){
                        begin()
                    }

                    if(begin_result){//
                        if(user_point_list <= 40){
                            pointer.opacity = 0 //
                        }

                        opacityToOne(outer_circle)
                        opacityToOne(yd)
                        if(hudu <360){
                            pointer.rotate(2.65, view.center)
                            hudu += 2.65
                        }
                        if(circle_line_list_length  != circle_line_list.length){

                        }else{
                            console.log('')
                            pointer.opacity = 0
                            begin_result = false //
                            addmooncake_font = true //
                        }
                        if(circle_line_list_length  <= circle_line_list.length / 1.5) opacityToZero(pointer,0.0083)


                        time += event.delta
                        if(time >= 0.016001){
                            //
                            console.log(circle_line_list.length)
                            if(circle_line_list_length < circle_line_list.length){
                                circle_line_list[circle_line_list_length].strokeColor = '#F59A55'
                                circle_line_list_length += 1
                            }
                            time = 0
                        }



                    }

                    if(addmooncake_font){
                        //
                        //alert('bg'+randomNum(1,4))
                        var s = 'bg'+randomNum(1,4)
                        var pic_mooncake = new Raster('bg1')
                        pic_mooncake.position = view.center
                        pic_mooncake.size = new Size(circleWidth(0.69)*2, circleWidth(0.69)*2)
                        users_path.strokeColor = '#8B572A'
                        var up =  users_path.clone()
                        //up.position = new Point(300,300)
                        up.strokeWidth = 2

                        var group = new Group([users_path, pic_mooncake])
                        group.setClipped(true)
                        group.fillColor = '#8B572A'

                        //
                        var favouriteMooncake = ['','','','','',
                            '','','','','',
                            '','','','',''
                            ,'','','','','',
                            '','','','',''
                        ][randomNum(0,12)]

                        var cc = 0.4;

                        addText(-88*cc+5 , -21*cc,favouriteMooncake.split('')[0])
                        addText(12*cc , -24*cc,favouriteMooncake.split('')[1])
                        addText(-88*cc+5 , 73*cc,favouriteMooncake.split('')[2])
                        addText(12*cc , 73*cc,favouriteMooncake.split('')[3])

                        addmooncake_font = false
                        begin_init = false


                        // pormiseok

                        endS(listLength)
                        changeToImage()
                        //console.log()


                        //var border = users_path.clone()

                        //
                        /*var outer_circle_border = new Path.Circle({
                            center: view.center,
                            radius: circleWidth(),
                            strokeColor: '#F59A55',
                            strokeWidth: 2
                        })*/

                        //var group2 = new Group([users_path, outer_circle])//
                        //group2.opacity = 0.5
                    }

                    //console.log(time)
                    //pointer.rotate(3, view.center)
                }

                function changeToImage(){
                    var dom = document.getElementById('center')
                    html2canvas(dom, {onclone: (document)=>{
                            document.getElementById('center').classList.add('changBG')
                            var svgImg = paper.project.exportSVG()
                            document.getElementById('center').innerHTML = ''
                            document.getElementById('center').appendChild(svgImg)

                        }}).then((canvas)=>{
                        localStorage.setItem('canvasSrc', canvas.toDataURL() )
                        console.log('??')
                        window.location.href = window.location.href.split('#')[0] + '#/result'
                        console.log('??2')
                    })
                }
                function addText(x=0, y=0,string = 'a'){
                    var text = new PointText(new Point(view.center.x + x, view.center.y + y));
                    //text.position =
                    text.fillColor = '#e26634';
                    text.fontSize = view.size.width/13;
                    text.color = '#e26634'
                    text.content = string;
                }

                function endS(s){
                    //alert(s)

                    switch(true){
                        case s<284*2.25:             localStorage.setItem('one','9');localStorage.setItem('lv','Lv9');localStorage.setItem('two',parseInt(  (  (284*2.25-s + 40*2.25) / (284**2.25))*10 ) );     localStorage.setItem('wa', '');break;
                        case s<750*2.25 && s>=284*2.25:   localStorage.setItem('one','8');localStorage.setItem('lv','Lv8');localStorage.setItem('two',parseInt(  (  (750*2.25-s) /((750-284)*2.25) )*10 ) );   localStorage.setItem('wa', '');break;
                        case s<1300*2.25 && s>=750*2.25:  localStorage.setItem('one','7');localStorage.setItem('lv','Lv7');localStorage.setItem('two',parseInt(  (  (1300*2.25-s) /((1300-750)*2.25) )*10 ) ); localStorage.setItem('wa', '');break;
                        case s<5850 && s>=1300*2.25: localStorage.setItem('one','6');localStorage.setItem('lv','Lv6');localStorage.setItem('two',parseInt(  (  (2600*2.25-s) /((2600-1300)*2.25) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<7425 && s>=5850: localStorage.setItem('one','5');localStorage.setItem('lv','Lv5');localStorage.setItem('two',parseInt(  (  (3300*2.25-s) /((3300-2600)*2.25) )*10 ) );localStorage.setItem('wa', '');break;

                        case s<9000 && s>=7425: localStorage.setItem('one','4');localStorage.setItem('lv','Lv4');localStorage.setItem('two',parseInt(  (  (4000*2.25-s) /((4000-3300) )*2.25)*10 ) );localStorage.setItem('wa', '');break;
                        case s<10125 && s>=9000: localStorage.setItem('one','3');localStorage.setItem('lv','Lv3');localStorage.setItem('two',parseInt(  (  (4500*2.25-s) /((4500-4000)*2.25) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<11250 && s>=10125: localStorage.setItem('one','2');localStorage.setItem('lv','Lv2');localStorage.setItem('two',parseInt(  (  (5000*2.25-s) /((5000-4500)*2.25) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<13050 && s>=11250: localStorage.setItem('one','1');localStorage.setItem('lv','Lv1');localStorage.setItem('two',parseInt(  (  (5800*2.25-s) /((5800-5000)*2.25) )*10 ) );localStorage.setItem('wa', '');break;
                        case s<144000 && s>=13050: localStorage.setItem('one','0');localStorage.setItem('lv','Lv1');localStorage.setItem('two',randomNum(0,9));localStorage.setItem('wa', '');break;
                    }
                    if(   s<(284*2.25) && parseInt(  (  (284*2.25-s + 40*2.25) /(284*2.25))*10 ) >= 6 ) {
                        localStorage.setItem('lv','Lv10');localStorage.setItem('wa', '');
                    }
                    console.log(parseInt(  (  (284-s + 40) /284)*10) + '')
                    console.log(user_point_list)

                    localStorage.setItem('ms',['','','','','',
                        '','','','','',
                        '','','','','',
                        '','','','',''][randomNum(0,12)])

                    /*if(user_point_list<4) {
                        localStorage.setItem('one', '0');
                        localStorage.setItem('lv', 'Lv1');
                        localStorage.setItem('two', randomNum(0,9));
                    }
                    if(user_point_list>=4 && user_point_list<17) {
                        localStorage.setItem('one', '1');
                        localStorage.setItem('lv', 'Lv1');
                        localStorage.setItem('two', randomNum(0,9));
                    }*/

                    console.log(s)


                    //284 90
                    //750 80
                    //130070
                    //260060
                    //330050
                    //400040
                    //450030
                    //500020
                    //580010
                    //64000
                }
            },
        },
        mounted(){

            this.anmination()
          try{
              this.paperStart()
          } catch(e){
                console.log(e)
          }

        }
    }
</script>

<style lang="scss" scoped>
    @function px2html($px){
        $rem: 37.5px;
        @return ($px / $rem) + rem;
    }

.main{
    height: 100%;
    width: 100%;
    position:fixed;
    .moon{
        position:absolute;
        top:px2html(130px);
        width:100%;
        height:px2html(360px);
    }
    .bottom-desc{
        color: #8B572A;
        font-size: px2html(14px);
        position: absolute;
        bottom:10vh;
        width:100vw;
        text-align: center;
        animation: opa ease-in 2s;
        animation-fill-mode: forwards;
    }
}
</style>
<style>
    .changBG{
        background: #fff7e4;
        font-size:20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
</style>